.creator.icon.clickable:hover {
  cursor: pointer; }

.creator.icon > .wrap > .label {
  margin-left: 6px; }

.creator.icon > .wrap-default {
  color: black; }

.creator.icon > .wrap-primary {
  color: #8b480e; }

.creator.icon > .wrap-success {
  color: #195f34; }

.creator.icon > .wrap-danger {
  color: #8c101b; }

.creator.icon > .wrap-warning {
  color: #ffbf00; }

.creator.icon > .wrap-info {
  color: #2894aa; }

.creator.icon > .wrap-light {
  color: #a19b9b; }

.creator.icon > .wrap-dark {
  color: #161616; }

.creator.icon > .wrap-antan {
  color: #fe2f00; }

.creator.icon > .wrap-notify {
  color: #07294e; }
